          <p>Считано от 29.03.2016 г. „Нет-Сърф.Нет” ООД стартира изпълнението на договор за безвъзмездна финансова помощ BG16RFOP002-2.001-1307-C01. Проектът „Подобряване на производствения капацитет на „НЕТ-СЪРФ.НЕТ” ООД“ се финансира по Оперативна програма „Иновации и конкурентоспособност“ 2014-2020 (ОПИК), като срокът за неговото изпълнение е 18 месеца. Общата стойност на проекта е в размер на 375 000 лева, като размерът на безвъзмездната финансова помощ е 70% или 262 500 лeвa. Съответно, размерът на европейското съфинансиране на проекта възлиза на 223 125 лeвa, а на националното съфинансиране - 39 375 лeвa. Проектът на „Нет-Сърф.Нет” ООД е насочен към постигане на следните две основни цели – Повишаване на производствения капацитет и Засилване на експортния потенциал на кандидата. С договорената БФП кандидатът планира да извърши инвестиции в дълготрайни материални активи, свързани с разширяване на капацитета на съществуващите стопански обекти на „Нет-Сърф.Нет” ООД в градовете Вършец и Монтана. Очакваните резултати вследствие изпълнението на проекта включват: подобряване на Интернет услугите, телевизионните, облачните (cloud) и PVR услуги на предприятието; разширяване на производствения капацитет за Интернет, телевизионни и облачни услуги с 12%; ръст в приходите от продажби (на Интернет, телевизионни и облачни услуги) на дружеството с 18% в средносрочен план; постигане на 2,8% дял на износа на услугите общо в приходите; в средносрочен план постигане на вътрешна норма на възвращаемост на проекта 181,28%; нарастване на производителността на труда с 15,07%; увеличаване на средните генерирани приходи от износ с 25,20% и увеличаване на ефективността на производствените разходи с 16,25%.</p>

