          <p>Считано от 26.04.2018 г. „Нет-сърф.нет” ООД стартира изпълнението на договор за безвъзмездна финансова помощ BG05M9OP001-1.022-0048-C01. Проектът „Осъществяване на специфични обучения в&nbsp; „НЕТ-СЪРФ.НЕТ” ООД“ се финансира по Оперативна програма „Развитие на човешките ресурси“ и срокът за неговото изпълнение е 11 месеца. Общата стойност на проекта е в размер на 376 612.50 лева, като размерът на безвъзмездната финансова помощ е 80% или 301 290.00 лeвa. Размерът на европейското съфинансиране на проекта възлиза на 256 096.50 лeвa, а на националното съфинансиране – 45 193.50 лeвa. Проектът на „Нет-сърф.нет” ООД е насочен към постигане на следните главна цел &nbsp;– повишаване на производителността на труда и адаптивността и създаване на условия за устойчива заетост&nbsp; в&nbsp; „НЕТ-СЪРФ.НЕТ”ООД чрез придобиване на нови специфични умения на служителите и безработни младежи посредством участие в специфични обучения съобразно текущото и бъдещето им работно място и нуждите на предприятието. Очакваните резултати вследствие изпълнението на проекта включват:</p>
